import { Component, OnInit } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-content-nav',
  templateUrl: './content-nav.component.html',
  styleUrls: ['./content-nav.component.css']
})
export class ContentNavComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    this.navhover();
  }
  navhover() {
    $("li.dropdown-li,li.big-li").each(function () {
        $(this).on("mouseout mouseover", function (event) {
            if (event.type === "mouseover") {
              $(this).addClass("open");
            }else if (event.type === "mouseout") {
              $(this).removeClass("open");
            }
        });
    }
    );
  }

}



