import {
    AfterViewInit,
    Component, ComponentFactory, ComponentFactoryResolver, ComponentRef, OnDestroy, OnInit, QueryList, ViewChild,
    ViewContainerRef
} from '@angular/core';
import {AddCommentComponent} from "../add-comment/add-comment.component";
import {Comment, VideosService} from "../share/videos.service";
declare var $: any;
@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.css']
})
export class CommentComponent implements OnInit, OnDestroy, AfterViewInit {
    isAddCommentOn = false;
    componentRef: ComponentRef<AddCommentComponent>;
    @ViewChild("addCommentContainer", {read: ViewContainerRef}) container: ViewContainerRef;

    public comments: Comment[];

  constructor(private resolver: ComponentFactoryResolver,
              private videoService: VideosService) { }

  ngOnInit() {
      this.comments = this.videoService.comments;
      this.selectCommentHead();
  }
  // 选择评论函数 ：全部/热门
  selectCommentHead() {
    const $select_items = $(".comment_container>.comment-content>.comment-select>.selection");
    $select_items.each(function () {
        $(this).on({
            "click": function () {
                $(this).addClass("on");
                $(this).siblings("span").removeClass("on");
            }
        });
    });
  }
  createAddComment(e) {
      console.log(e.srcElement);
      this.isAddCommentOn = !this.isAddCommentOn;
      if (this.isAddCommentOn) {
          this.container.clear();
          const factory: ComponentFactory<AddCommentComponent> = this.resolver.resolveComponentFactory(AddCommentComponent);
          this.componentRef = this.container.createComponent(factory);
      }else {
          this.container.clear();
      }
  }
  ngOnDestroy() {
      if (this.componentRef) {
          this.componentRef.destroy();
      }
  }
  ngAfterViewInit(): void {
  }
}



