import { Component, OnInit } from '@angular/core';
import {Video, VideosService} from "../share/videos.service";
import {ActivatedRoute, Params} from "@angular/router";
declare var $: any;
@Component({
  selector: 'app-videoplay',
  templateUrl: './videoplay.component.html',
  styleUrls: ['./videoplay.component.css']
})
export class VideoplayComponent implements OnInit {
  public video: Video = null;
  private id = 0;
  private timer;

  constructor(private videoService: VideosService,
              private router: ActivatedRoute) { }

  ngOnInit() {
      $("html,body").scrollTop(0);
    this.router.params.subscribe((params: Params) => {
      this.id = parseInt(params['id'], 0);
    });
    console.log(this.id);
    this.video = this.videoService.getVideoById(this.id);
  }
  // 收藏动画特效
  starAnimate(e) {
    let positionX = parseInt(e.srcElement.style.backgroundPositionX, 0) || 0;
    this.timer = setInterval(() => {
      if (positionX < 1040) {
        positionX = positionX + 80;
        e.srcElement.style.backgroundPositionX = `-${positionX}px`;
      }else {
        positionX = 0;
        e.srcElement.style.backgroundPositionX = `-${positionX}px`;
      }
    }, 100);
    console.log(parseInt(e.srcElement.style.backgroundPositionX, 0));
  }
  starAnimateEnd(e) {
    clearInterval(this.timer);
    e.srcElement.style.backgroundPositionX = '-0px';
  }
//  硬币动画特效
  coinAnimate(e) {
      let flag = false;
      let positionX = parseInt(e.srcElement.style.backgroundPositionX, 0) || 0;
      this.timer = setInterval(() => {
        if (!flag) {
          if (positionX < 480) {
              positionX = positionX + 80;
              e.srcElement.style.backgroundPositionX = `-${positionX}px`;
          }else {
              flag = true;
          }
        }
        if (flag) {
          if (positionX > 0) {
              positionX = positionX - 80;
              e.srcElement.style.backgroundPositionX = `-${positionX}px`;
          } else {
              flag = false;
          }
        }
      }, 100);
      console.log(parseInt(e.srcElement.style.backgroundPositionX, 0));
  }
  coinAnimateEnd(e) {
      clearInterval(this.timer);
      e.srcElement.style.backgroundPositionX = '-0px';
  }
//  小电视动画特效
  tvAnimate(e) {
      let positionX = parseInt(e.srcElement.style.backgroundPositionX, 0) || 0;
      this.timer = setInterval(() => {
          if (positionX < 880) {
              positionX = positionX + 80;
              e.srcElement.style.backgroundPositionX = `-${positionX}px`;
          }else {
              positionX = 0;
              e.srcElement.style.backgroundPositionX = `-${positionX}px`;
          }
      }, 100);
  }
  tvAnimateEnd(e) {
      clearInterval(this.timer);
      e.srcElement.style.backgroundPositionX = '-0px';
  }
//  电池特效
    batteryAnimate(e) {
        let positionX = parseInt(e.srcElement.style.backgroundPositionX, 0) || 0;
        this.timer = setInterval(() => {
            if (positionX < 320) {
                positionX = positionX + 80;
                e.srcElement.style.backgroundPositionX = `-${positionX}px`;
            }else {
                positionX = 0;
                e.srcElement.style.backgroundPositionX = `-${positionX}px`;
            }
        }, 100);
    }
    batteryAnimateEnd(e) {
        clearInterval(this.timer);
        e.srcElement.style.backgroundPositionX = '-0px';
    }
}



