import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {Observable} from "rxjs/Observable";
import {Http} from "@angular/http";
import 'rxjs/Rx';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
   // 注册界面表单
   formModule1: FormGroup = new FormGroup({
      username: new FormControl('', [Validators.required, Validators.minLength(6)]),
      passwordGroup: new FormGroup({
          password: new FormControl('', [Validators.required, Validators.minLength(6)]),
          passwordConfirm: new FormControl('', [Validators.required, Validators.minLength(6)])
      }, this.equalPassword)
  });
   formModule2: FormGroup = new FormGroup({
       username_login: new FormControl('', [Validators.required, Validators.minLength(6)]),
       password_login: new FormControl('', [Validators.required, Validators.minLength(6)])
   });
   // 登录界面表单，采用表单构造器
   fb: FormBuilder = new FormBuilder();
  // 服务器返回流
  req: Observable<any>;
  constructor(private http: Http) {
      const body = JSON.stringify(this.formModule1.value);
      this.req = this.http.post('api/sign', body).map(res => res.json());
  }

  ngOnInit() {
  }

  onSignSubmit() {
    if (this.formModule1.valid) {
        this.req.subscribe((res) => {
            console.log(res);
        });
        console.log(this.formModule1.valid);
    } else {
      return false;
    }
  }
  onLogin() {
      if (this.formModule2.valid) {
          console.log(`登录请求发送${this.formModule2.valid}`);
      } else {
          console.log(this.formModule2.errors);
          return false;
      }
  }
  //////////密码验证////////////////
  equalPassword(group: FormGroup): any {
    const password: FormControl = group.get("password") as FormControl;
    const passwordConfirm: FormControl = group.get("passwordConfirm") as FormControl;
    const valid: boolean = (password.value === passwordConfirm.value);
    return valid ? null : {passwordNoEquail: true}
  }

}



