import { Component, OnInit } from '@angular/core';
import {Video, VideosService} from '../share/videos.service';

@Component({
  selector: 'app-live-broadcast',
  templateUrl: './live-broadcast.component.html',
  styleUrls: ['./live-broadcast.component.css']
})
export class LiveBroadcastComponent implements OnInit {
  public videos: Video[] = [];

  constructor(private videoService: VideosService) {
    this.videos = this.videoService.getLiveVideos();
  }

  ngOnInit() {
  }

}



