import { Component, OnInit } from '@angular/core';
import {Video, VideosService} from "../share/videos.service";

@Component({
  selector: 'app-china-cartoon',
  templateUrl: './china-cartoon.component.html',
  styleUrls: ['./china-cartoon.component.css']
})
export class ChinaCartoonComponent implements OnInit {
  public videos: Video[] = [];

  constructor(private videoService: VideosService) { }

  ngOnInit() {
    this.videos = this.videoService.getDanceVideos();
  }

}



