    public isGuiChu: boolean;
    public isSceince: boolean;
    public isFashon: boolean;
    public isAD: boolean;
    public isFilm: boolean;
    public isEntertainment: boolean;
    constructor (
        isBroadcast: boolean,
        isSpread: boolean,
        isCartoon: boolean,
        isChina: boolean,
        isMusic: boolean,
        isDance: boolean,
        isGame: boolean,
        isLife: boolean,
        isGuiChu: boolean,
        isSceince: boolean,
        isFashion: boolean,
        isAD: boolean,
        isFilm: boolean,
        isEntertainment: boolean
    ) {
        this.isBroadcast = isBroadcast;
        this.isSpread = isSpread;
        this.isCartoon = isCartoon;
        this.isChina = isChina;
        this.isMusic = isMusic;
        this.isDance = isDance;
        this.isGame = isGame;
        this.isLife = isLife;
        this.isGuiChu = isGuiChu;
        this.isSceince = isSceince;
        this.isFashon = isFashion;
        this.isAD = isAD;
        this.isFilm = isFilm;
        this.isEntertainment = isEntertainment;
    };
