import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HeaderComponent } from './header/header.component';
import { ContentNavComponent } from './content-nav/content-nav.component';
import { AdComponent } from './ad/ad.component';
import { HomeComponent } from './home/home.component';
import { SpreadComponent } from './spread/spread.component';
import {VideosService} from "./share/videos.service";
import { FooterComponent } from './footer/footer.component';
import { SideNavbarComponent } from './side-navbar/side-navbar.component';
import { LiveBroadcastComponent } from './live-broadcast/live-broadcast.component';
import { CartoonComponent } from './cartoon/cartoon.component';
import { MusicComponent } from './music/music.component';
import { DanceComponent } from './dance/dance.component';
import {Router, RouterModule, Routes} from "@angular/router";
import { VideoplayComponent } from './videoplay/videoplay.component';
import { CommentComponent } from './comment/comment.component';
import { AddCommentComponent } from './add-comment/add-comment.component';
import { ChinaCartoonComponent } from './china-cartoon/china-cartoon.component';
import { GameComponent } from './game/game.component';
import {ControlContainer, FormsModule, ReactiveFormsModule} from "@angular/forms";
import {HttpClient} from "selenium-webdriver/http";
import {Http, HttpModule} from "@angular/http";
const routerConfig: Routes = [
    {path: '', component: HomeComponent},
    {path: 'videoplay/:id', component: VideoplayComponent},
    {path: '**', component: HomeComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HeaderComponent,
    ContentNavComponent,
    AdComponent,
    HomeComponent,
    SpreadComponent,
    FooterComponent,
    SideNavbarComponent,
    LiveBroadcastComponent,
    CartoonComponent,
    MusicComponent,
    DanceComponent,
    VideoplayComponent,
    CommentComponent,
    AddCommentComponent,
    ChinaCartoonComponent,
    GameComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routerConfig),
    ReactiveFormsModule,
      FormsModule,
      HttpModule
  ],
  providers: [VideosService],
  bootstrap: [AppComponent],
  entryComponents: [AddCommentComponent]
})
export class AppModule { }



